import React, { useEffect, useRef } from "react";
import katex from "katex";

const removeAllChildren = el => {
  while (el.hasChildNodes()) {
    el.removeChild(el.childNodes[0]);
  }
};

const createPartElement = container => {
  const partEl = document.createElement("div");
  partEl.classList.add("part");
  container.appendChild(partEl);
  return partEl;
};

const renderText = (el, text) => {
  // This isn't valid LaTeX. Just assume it's regular text.
  el.innerText = text.replace(/^!/, "");
  el.classList.add("text-part");
};

const renderLatex = (el, text) => {
  katex.render(
    text
      .split("\n")
      // Add a newline to each line since we do that anyway.
      .map(l => l + " \\\\")
      .join("\n"),
    el
  );
  el.classList.add("latex-part");
};

const Renderer = ({ text }) => {
  const contentRef = useRef();

  useEffect(() => {
    const el = contentRef.current;
    const parts = text.split("\n\n").filter(p => p.trim());

    removeAllChildren(el);

    // Render the new parts.
    parts.forEach(part => {
      const partEl = createPartElement(el);

      if (part.startsWith("!")) {
        renderText(partEl, part);
      } else {
        try {
          renderLatex(partEl, part);
        } catch (e) {
          renderText(partEl, part);
        }
      }
    });
  }, [text]);

  return (
    <div className="Renderer">
      <div ref={contentRef} />
    </div>
  );
};

export default Renderer;
